<template>
  <DashNav />
  <div class="l flex justify-center items-center min-h-[100vh]">
    <div
      class="j w-[500px] min-h-[400px] rounded-2xl shadow-2xl max-md:w-[90vw]"
    >
      <div class="L flex p-2 mb-[.5cm]">
                     <i class="fa fa-arrow-circle-left"  @click="$store.dispatch('historygoback')" style="color:black;"></i>

        <div class="l flex-grow text-center font-semibold">Withdraw BITCOIN</div>
      </div>

      <div class="flex justify-between px-2">
        <span class="l capitalize text-gray-400">you send</span>
        <span @click="setmax" class="l capitalize text-green-600">max</span>
      </div>

      <div
        class="min-h-[3cm] bg-green-100 mt-2 w-[90%] rounded-2xl mx-auto border-0 hover:border-1 hover:border-green-500"
      >
        <div class="l flex gap-[10px] items-center p-2">
          <div
            class="r w-[.8cm] h-[.8cm] rounded-full flex items-center justify-center bg-black text-white"
          >
            <img
              src="/img/BTC.ff692282.svg"
              alt="+"
              class="l w-[100%] rounded-full"
            />
          </div>

          <div class="k flex flex-col">
            <p class="k font-semibold text-sm text-gray-500">BITCOIN</p>
          </div>
        </div>

        <input
           @input="($event)=>{$store.commit('setwithdrawamount',$event.target.value)}"

          id="amount"
          type="number"
          class="p-2 font-bold text-2xl outline-0"
          placeholder="0.00000000 BTC"
        />
      </div>
      <small v-if="error" class="l text-[red] font-semibold ms-[1cm] block"
        >{{ error }}
      </small>

      <div class="flex justify-between px-2">
        <span class="l capitalize text-gray-400">available BITCOIN</span>
        <span class="l capitalize font-semibold">{{ user.ballance }}</span>
      </div>

      <div class="l flex justify-center items-center mt-2">
        <input
          type="text"
          class="bg-gray-300 w-[70%] p-2 wadddr outline-0"
          placeholder="receiver's wallet address"
          id="amount"
        />
        <button
          @click="pasteaddress"
          class="active:bg-white active:text-black capitalize p-2 bg-black text-white"
        >
          paste
        </button>
      </div>

      <button
        @click="sendmoney"
        class="l capitalize bg-gray-300 mx-auto w-[80%] h-[1.2cm] flex items-center justify-center mt-[1cm]"
      >
      <Skeleton v-if="loading" text="Sending"/>
        <span v-else>{{ sendbuthtext }}</span>
      </button>

      <small class="l text-gray-600 font-semibold text-center block"
        >1 BITCOIN ~ USD 97,652.57
      </small>
    </div>
  </div>
  <DashfooterPage />
</template>

<script>
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";
import Skeleton from "@/components/SkeletonPage.vue";

export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
     if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },

  name: "SendPage",
  components: {
    DashNav,
    DashfooterPage,
    Skeleton,
  },
  data() {
    return {
      error: false,
      user: {
        firstname: "",
        lastname: "",
        email: "",
        country: "",
        currency: "",
        ballance: "",
      },
      loading: false,
      sendbuthtext: 'Withdraw',
    };
  },

  mounted() {
 let userdata = this.$store.state.user
    this.user = userdata[0]
  },
  methods: {
   async sendmoney() {
      if (document.querySelector("#amount").value > this.user.ballance) {
        this.error = "Insufficient ballance";
      } else if (document.querySelector("#amount").value == 0) {
        this.error = "Amount should be greater than 0";
      }
      else {
        this.loading = true

      let formData = new FormData();
      formData.append("message", "withddraw");
      formData.append("amount", this.$store.state.withdrawamount);
      formData.append("email", this.user.email);

      try {
        let csrfrequest = await fetch(
          "https://etoroinvestback.tweblinks.com/sanctum/csrf-token",
          {
            credentials: "include",
          }
        );
        let csrfdata = await csrfrequest.json();
        let request = await fetch("https://etoroinvestback.tweblinks.com/api/register", {
          headers: { "X-CSRF-TOKEN": csrfdata.token },
          credentials: "include",
          method: "post",
          body: formData,
        });
        let data = await request.json();
        this.$store.commit('UpateUsertransaction',data.transaction)
        this.$store.commit('UpateUserballance',data.ballance)
        document.querySelector('#amount').value=''
      } catch (error) {
        console.log(error);
      }

      setTimeout(() => {
        this.loading=false
      },2000)
      this.sendbuthtext='Sent'
      }
     

    },
    setmax() {
      document.querySelector("#amount").value = this.user.ballance;
    },
    pasteaddress() {
      navigator.clipboard
        .readText()
        .then((text) => {
          document.querySelector(".wadddr").value = text;
        })
        .catch((error) => {
          console.error("Error pasting text: ", error);
        });
    },
  },
};
</script>
