<script>
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";
export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
      if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },
  name: "PlanPage",
  components: {
    DashNav,
    DashfooterPage,
  },
  data() {
    return {
      selectedplan: null,

      interest: "",
      mininvest: "",
      maxinvest: "",
    };
  },
  methods: {
    selectplan(event) {
      console.log(event.currentTarget.querySelector(`.min`));
      this.mininvest = event.currentTarget.querySelector(`.min`).innerText;
      this.maxinvest = event.currentTarget.querySelector(`.max`).innerText;
      this.selectedplan = event.currentTarget.id;
      this.interest = event.currentTarget.querySelector("input").value;

      this.$router.push({
        path: "/dashboard/Invest",
        query: {
          interest: this.interest,
          plan: this.selectedplan,
          min: this.mininvest,
          max: this.maxinvest,
        },
      });
    },
  },
};
</script>
