          let csrfrequest = await fetch(
            "https://etoroinvestback.tweblinks.com/sanctum/csrf-token",

            {
              credentials: "include",
            }
          );
          let csrfdata = await csrfrequest.json();
          let request = await fetch(
            "https://etoroinvestback.tweblinks.com/api/register",
            {
              headers: {
                "content-type": "application/json",
                "X-CSRF-TOKEN": csrfdata.token,
              },
              method: "post",
              credentials: "include",
              body: JSON.stringify(this.userData),
            }
          );
          let data = await request.json();
          console.log(data);
          if (typeof data == "object") {
            this.loading = false;
            if (data.message == "User registered sucesfull") {
              router.push("/login");
            } else if (data.message == "User already registered") {
              this.$store.commit("setError", data.message);
            }
          } else {
            this.$store.commit("setError", "registration failed");
          }
        } catch (error) {
          console.log(error);
        } finally {
          console.log(this.userData);
        }
      } else {
        console.log("password doesnt match");
      }
    },
  },
  setup() {
    onMounted(() => {});

    return {};
  },
};
</script>

