<script setup>
/* global google */
import { ref, onMounted } from "vue";

const defaultLanguage = ref("tr");

function loadGoogleTranslateScript() {
  if (window.google?.translate) return;

  const script = document.createElement("script");
  script.src =
    "//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit";
  script.async = true;
  document.head.appendChild(script);

  window.googleTranslateElementInit = () => {
    setTimeout(() => {
      new google.translate.TranslateElement(
        {
          pageLanguage: defaultLanguage.value,
          layout: google.translate.TranslateElement.InlineLayout.HORIZONTAL, // dropdown style
          autoDisplay: false,
        },
        "google_translate_element"
      );
    }, 500);
  };
}

onMounted(() => loadGoogleTranslateScript());
</script>

<template>
  <div class="">
    <div
      class="l max-w-[100vw] max-h-[100vh] overflow-scroll fixed bottom-0 left-0 z-[1000000000000000000] border-amber-400 border"
    >
      <div
        id="google_translate_element"
        class="fixed top-[70vh] left-0 bg-white p-2 rounded shadow"
      ></div>
    </div>
  </div>
</template>

