<template>
  <DashNav/>
  <div class="l flex justify-center items-center min-h-[100vh]">
    <div
      class="j w-[500px] min-h-[400px] rounded-2xl shadow-2xl max-md:w-[90vw]"
    >
      <div class="L flex p-2 mb-[.5cm]">
               <i class="fa fa-arrow-circle-left"  @click="$store.dispatch('historygoback')" style="color:black;"></i>

      </div>

      <p class="text-2xl font-semibold text-center w-[60%] mx-auto">
        How much would you like to deposit
      </p>

      <div
        class="min-h-[3cm] bg-green-100 mt-2 w-[90%] rounded-2xl mx-auto border-0 hover:border-1 hover:border-green-500"
      >
        <div class="l flex gap-[10px] items-center p-2">
          <div
            class="r w-[.8cm] h-[.8cm] rounded-full flex items-center justify-center bg-black text-white"
          >
            <img src="/img/BTC.ff692282.svg" alt="+" class="l w-[100%] rounded-full" />
          </div>

          <div class="k flex flex-col">
            <p class="k font-semibold text-sm text-gray-500">BITCOIN</p>
          </div>
        </div>

        <input
        @input="($event)=>{$store.commit('setdepositeamount',$event.target.value)}"
          type="number"
          min="0"
          class="p-2 font-bold text-2xl outline-0 amount"
          placeholder="0.00000000 BTC"
        />
      </div>
      <small v-if="error" class="l text-[red] font-semibold ms-[1cm] block"
        >{{ error }}
      </small>

      <div
        class="l sbuth capitalize bg-gray-300 mx-auto w-[80%] h-[1.2cm] flex items-center justify-center mt-[1cm]"
      >
        Min: BITCOIN 0.00000032 | Max : BITCOIN unlimited
      </div>
                <div class="flex  justify-center items-center">

     <button

        class="l capitalize bg-gray-300 mx-auto w-[80%] h-[1.2cm] flex items-center justify-center mt-[1cm]"
               @click="checkerror"

        >
        deposit
      </button>
            </div>

     
      <small class="l text-gray-600 font-semibold text-center block"
        >1 BITCOIN ~ USD 97,652.57
      </small>
    </div>
  </div>

  <DashfooterPage/>
</template>

<script>
import DashfooterPage from "@/components/DashfooterPage.vue";
import DashNav from "@/components/DashNav.vue";


export default {
  beforeRouteEnter(to, from, next) {
  next(component => {
    if (!component.$store.state.user) {
        component.$router.push("/login");
      }
  })
}, 

  name: "DepositeusdPage",
  components: {
    DashfooterPage,
    DashNav,
  },
  data() {
    return {
      error: false,
    }
  },
  methods: {
    checkerror(){
      if (document.querySelector('.amount')?.value < 0.00000032) {
        this.error = 'minimum deposite 0.00000032 BITCOIN'
      
      }
      else {
        this.$router.push('/dashboard/receive')
      }
    }
  }
};
</script>
