<template>
  <NavPage position="relative" />

  <div
    class="cnt min-h-[50vh] max-md:flex-col flex justify-center items-center"
  >
    <div
      class="l mt-[1.5cm] w-[50%] max-md:w-[100%] h-[50vh] flex justify-center items-center"
    >
      <form
        @submit.prevent="loginUser"
        class="border-black flex flex-col w-[80%] p-[1cm] min-h-[200px] shadow shadow-gray-600"
        style=""
      >
        <label class="font-semibold block p-2">Email</label>
        <input
          type="email"
          v-model="userData.email"
          class="p-2 outline-0 border-blue-300 border-2 w-full"
        />

        <label class="font-semibold block p-2">Password</label>
        <input
          type="password"
          v-model="userData.password"
          class="p-2 outline-0 border-blue-300 border-2 w-full"
        />

        <button
          class="py-2 px-4 rounded active:text-black active:bg-white bg-black text-white mt-2"
        >
          <SkeletonPage v-if="loginloading" :text="logintext" />
          <span v-else> Log In </span>
        </button>
        <small class="font-semibold"
          >Dont have an account?
          <router-link to="/signup">
            <a class="text-blue-500"> Sign up</a></router-link
          ></small
        >
        <small class="err font-semibold text-red-700">{{
          $store.state.error
        }}</small>
      </form>
    </div>
    <div class="l w-[40%] h-[400px] max-md:w-[100vw]">
      <img src="/img/m6.jpg" alt="" class="w-full h-full block" />
    </div>
  </div>
</template>

<script>
import NavPage from "@/components/NavPage.vue";
import SkeletonPage from "@/components/SkeletonPage.vue";
export default {
  name: "LoginPage",
  components: {
    NavPage,
    SkeletonPage,
  },

  data() {
    return {
      userData: {
        email: "",
        password: "",
        message: "Login",
      },
      logintext: "Loging in",
      loginloading: false,
    };
  },

  methods: {
    async loginUser() {
      try {
        this.loginloading = true;
        const csrfResponse = await fetch("https://etoroinvestback.tweblinks.com/sanctum/csrf-token",
          {
            credentials: "include",
          }
        );
        const csrfToken = await csrfResponse.json();

        let response = await fetch("https://etoroinvestback.tweblinks.com/api/register",
          {
            headers: {
              "Content-Type": "application/json",
              "X-CSRF-TOKEN": csrfToken.token, // Include CSRF token
            },
            credentials: "include",
            method: "POST",
            body: JSON.stringify(this.userData),
          }
        );

        let data = await response.json();

        if (data) {
          this.loginloading = false;

        }
        if (typeof data.message == "object") {
          this.$store.commit("UpateUser",data.message);

          this.$router.push("/dashboard/home");
        } else {
          this.$store.commit("setError", data.message);
        }
      } catch (error) {
        console.log(error);
      } 
    },
  },
  mounted() {
    this.$store.commit('setError','')
  },
};
</script>

<style scoped></style>
