<template>
  <DashNav />
  <div class="l flex justify-center items-center min-h-[100vh]">
    <div class="j w-[500px] rounded-2xl shadow-2xl max-md:w-[90vw]">
      <div class="L flex p-2 mb-[.5cm]">
                      <i class="fa fa-arrow-circle-left"  @click="$store.dispatch('historygoback')" style="color:black;"></i>

        <div class="l flex-grow text-center font-semibold">Invest BTC</div>
      </div>

      <div class="flex justify-between px-2">
        <span class="l capitalize text-gray-400"
          >your {{ $route.query.plan }} Investment</span
        >
        <span @click="setmax" class="l capitalize text-green-600">max</span>
      </div>

      <div
        class="min-h-[3cm] bg-green-100 mt-2 w-[90%] rounded-2xl mx-auto border-0 hover:border-1 hover:border-green-500"
      >
        <div class="l flex gap-[10px] items-center p-2">
          <div
            class="r w-[.8cm] h-[.8cm] rounded-full flex items-center justify-center bg-black text-white"
          >
            <img
              src="/img/BTC.ff692282.svg"
              alt="+"
              class="l w-[100%] rounded-full"
            />
          </div>

          <div class="k flex flex-col">
            <p class="k font-semibold text-sm text-gray-500">BITCOIN</p>
          </div>
        </div>

        <input
          id="amount"
          type="number"
          class="p-2 font-bold text-2xl outline-0"
          placeholder="0.00000000 BTC"
        />
      </div>
      <small v-if="error" class="l text-[red] font-semibold ms-[1cm] block"
        >{{ error }}
      </small>

      <div class="flex justify-between px-2">
        <span class="l capitalize text-gray-400">available BITCOIN</span>
        <span class="l capitalize font-semibold">{{ user.ballance }}</span>
      </div>

      <button
        @click="sendmoney"
        class="l capitalize bg-gray-300 mx-auto w-[80%] h-[1.2cm] flex items-center justify-center mt-[1cm]"
      >
        <Skeleton v-if="loading" text="Processing" />
        <span v-else>{{ sendbuthtext }}</span>
      </button>

      <small class="l text-gray-600 font-semibold text-center block"
        >1 BITCOIN ~ USD 97,652.57
      </small>
    </div>
  </div>
  <DashfooterPage />
</template>

<script>
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";
import Skeleton from "@/components/SkeletonPage.vue";

export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
      if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },

  name: "InvestPage",
  components: {
    DashNav,
    DashfooterPage,
    Skeleton,
  },
  data() {
    return {
      error: false,
      user: {
        firstname: "",
        lastname: "",
        email: "",
        country: "",
        currency: "",
        ballance: "",
      },
      loading: false,
      sendbuthtext: "Invest",
    };
  },

  mounted() {
    let userdata = this.$store.state.user;
    this.user = userdata[0];
  },
  methods: {
   async sendmoney() {
      let proceed1 = true;
      let proceed2 = true;

      if (
        Number(document.querySelector("#amount").value) <
        Number(this.$route.query.min)
      ) {
        this.error = `minimum deposit ${this.$route.query.min} BITCOIN `;
        proceed1 = false;
      }
      if (Number(document.querySelector("#amount").value) > Number(this.user.ballance) ) {
        this.error = "Insufficient ballance";
        proceed2 = false;
      }

      if (proceed1&&proceed2) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
        }, 2000);
        this.$store.commit('UpateUserballance',this.user.ballance-Number(document.querySelector("#amount").value))

        let csrfresponse = await fetch("https://etoroinvestback.tweblinks.com/sanctum/csrf-token", {
          credentials:'include',
        })
        let csrfdata = await csrfresponse.json()

        let data = {
          message: 'update ballance',
          amount: this.user.ballance,
          email:this.user.email,
        }
        let request = await fetch("https://etoroinvestback.tweblinks.com/api/register", {
          headers: { 'content-type': 'application/json', 'X-CSRF-TOKEN': csrfdata.token },
          credentials: 'include',
          method: 'post',
          body:JSON.stringify(data)
        })
        let response = await request.json()

        console.log(response)
       
        this.sendbuthtext = "Done";
        document.querySelector("#amount").value=''
      }
    },
    setmax() {
      document.querySelector("#amount").value = this.user.ballance;
    },
  },
};
</script>
