<script>
import VueApexCharts from "vue3-apexcharts";
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";

export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
      if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },

  name: "DHomePage",
  components: {
    apexchart: VueApexCharts,
    DashNav,
    DashfooterPage,

  },
  data() {
    return {
      series: [
        {
          name: "BTC",
          type: "column",
          data: [23, 11, 22, 27, 13, 22, 37, 21, 44, 22, 30],
        },
        {
          name: "BTC",
          type: "area",
          data: [44, 55, 41, 67, 22, 43, 21, 41, 56, 27, 43],
        },
        {
          name: "ETH",
          type: "line",
          data: [30, 25, 36, 30, 45, 35, 64, 52, 59, 36, 39],
        },
      ],
      chartOptions: {
        chart: {
          height: 350,
          type: "line",
          stacked: false,
          toolbar: {
            show: false,
          },
        },
        stroke: {
          width: [0, 2, 5],
          curve: "smooth",
        },
        plotOptions: {
          bar: {
            columnWidth: "50%",
          },
        },
        fill: {
          opacity: [0.85, 0.25, 1],
          gradient: {
            inverseColors: false,
            shade: "light",
            type: "vertical",
            opacityFrom: 0.85,
            opacityTo: 0.55,
            stops: [0, 100],
          },
        },
        labels: [
          "01/01/2024",
          "02/01/2024",
          "03/01/2024",
          "04/01/2024",
          "05/01/2024",
          "06/01/2024",
          "07/01/2024",
          "08/01/2024",
          "09/01/2024",
          "10/01/2024",
          "11/01/2024",
        ],
        markers: {
          size: 0,
        },
        xaxis: {
          type: "datetime",
        },
        yaxis: {
          title: {
            // text: 'Points',
          },
        },
        tooltip: {
          shared: true,
          intersect: false,
          y: {
            formatter: function (y) {
              if (typeof y !== "undefined") {
                return y.toFixed(0) + " points";
              }
              return y;
            },
          },
        },
      },
      user: {
        firstname: "",
        ballance: "",
      },
      stringtime: new Date().getHours(),
      transactions: [
        {
          type: "deposite",
          trtype: "",
          coin: "BTC",
          amount: "3500",
          status: "pending",
          statuscolor: "",
          img:'',
        },
      ],
      notransaction: true,
     
    };
  },

  mounted() {

  
    let userdata = this.$store.state.user;
    this.user = userdata[0];
    this.transactions = userdata[1];

   

    this.transactions.forEach((transaction) => {
      if (transaction.type == "deposite") {
        transaction.trtype = "received cash";
        transaction.img = "/img/receive.svg";
      } else if (transaction.type == "withdraw") {
        transaction.trtype = "cash withrawal";
        transaction.img = "/img/send.svg";
      }
      if (transaction.status == "pending") {
        transaction.statuscolor = "color:yellow;";
      } else if (transaction.status == "cancled") {
        transaction.statuscolor = "color:red;";
      } else if (transaction.status == "success") {
        transaction.statuscolor = "color:green;";
      }
    });

    this.mygreetings();
    if (this.transactions.length>0) {
      this.notransaction = false;
    } else {
      this.transactions = false;
      this.notransaction = true;
    }
  },
  methods: {
    mygreetings() {
      if (this.stringtime < 12) {
        this.stringtime = "morning";
      } else if (this.stringtime >= 12 && this.stringtime < 18) {
        this.stringtime = "afternoon";
      } else if (this.stringtime >= 18 && this.stringtime <= 24) {
        this.stringtime = "night";
      }
    },
    togglevisibility(event) {
      let ballance = document.querySelector(".accball");
      if (event.currentTarget.classList.contains('fa-eye')) {
        ballance.innerText = "***";
       event.currentTarget.classList.remove('fa-eye')
       event.currentTarget.classList.add('fa-eye-slash')
      } else {
        ballance.innerText = this.user.ballance;
        event.currentTarget.classList.add('fa-eye')
               event.currentTarget.classList.remove('fa-eye-slash')

      }
    },
  },
  setup() {
    return {};
  },
};
</script>

