import { createRouter, createWebHashHistory } from "vue-router";
import Home from "@/views/HomePage.vue";
import Login from "@/views/LoginPage.vue";
import Signup from "@/views/SignupPage.vue";
import DashHome from "@/views/dashboard/HomePage.vue";
import WalletPage from "@/views/dashboard/WalletPage.vue";
import ReceivePage from "@/views/dashboard/DepositePage.vue";
import DepositePage from "@/views/dashboard/DepositeusdPage.vue";
import SendPage from "@/views/dashboard/SendPage.vue";
import InvestPage from "@/views/dashboard/InvestPage.vue";
import ProfilePage from "@/views/dashboard/ProfilePage.vue";
import SupportPage from "@/views/dashboard/SupportPage.vue";
import PlanPage from "@/views/dashboard/PlanPage.vue";

const routes = [
  { path: "/", component: Home,},
  { path: "/login", component: Login,},
  { path: "/signup", component: Signup,},
  { path: "/dashboard/home", component: DashHome,},
  { path: "/dashboard/wallet", component: WalletPage,},
  { path: "/dashboard/receive", component: ReceivePage,},
  { path: "/dashboard/Send", component: SendPage,},
  {path: "/dashboard/plan",component: PlanPage,},
  {path: "/dashboard/Invest",component: InvestPage,},
  { path: "/dashboard/usd", component: DepositePage,},
  { path: "/dashboard/profile", component: ProfilePage,},
  { path: "/dashboard/support", component:SupportPage,},
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to) {
    if (to.hash) {
      return {
        el: to.hash,
        behavior: "smooth",
      };
    
    }
    return {
      top:0
    }
  }
});

export default router;
