import router from "@/router/router";
import { createStore } from "vuex";
import VuexPersistence from "vuex-persist";


const vuexlocale = new VuexPersistence({
    Storage:window.localStorage
})

const store = createStore({
  state() {
    return {
      error: "",
      user: false,
      depositeamount: "",
      withdrawamount: "",
      countries: [
        { code: "en|af", title: "Afrikaans" },
        { code: "en|ar", title: "Arabic" },
        // Add more languages as needed
      ],
    };
  },
  mutations: {
    updateprofile(state, payload) {
      state.user[0].profilepic = payload;
    },
    setError(state, payload) {
      state.error = payload;
    },
    UpateUser(state, payload) {
      state.user = payload;
    },
    setdepositeamount(state, payload) {
      state.depositeamount = payload;
      console.log(state.depositeamount);
    },
    setwithdrawamount(state, payload) {
      state.withdrawamount = payload;
      console.log(state.depositeamount);
    },
    UpateUsertransaction(state, payload) {
      state.user[1] = payload;
    },
    UpateUserballance(state, payload) {
      state.user[0].ballance = payload;
    },
  },
  actions: {
    historygoback() {
      router.go(-1);
    },
    logout() {
      localStorage.removeItem("user");
      router.push("/login");
    },
  },
  getters: {},
  plugins: [vuexlocale.plugin],
});


export default store