<template>
<DashNav/>
<div class="l flex justify-center items-center min-h-[80vh]">
    <div class="j w-[500px] min-h-[400px] rounded-2xl shadow-2xl max-md:w-[90vw]">
      <div class="L flex p-2 mb-[.5cm]">
                       <i class="fa fa-arrow-circle-left"  @click="$store.dispatch('historygoback')" style="color:black;"></i>

        <div class="l flex-grow text-center font-semibold">Profile Details</div>
      </div>

     <div class="l flex gap-[10px] items-center justify-start ps-[1cm]">
          <div
            class="r w-[4cm] h-[4cm] rounded-full flex items-center justify-center bg-black text-white"
          >
          <label for="profilepic" class=" w-[100%] h-[100%] bg-gray-400 rounded-full">
            <img :src="profile" alt="+" class="l w-[100%] h-[100%] block rounded-full" />

          </label>
            <input type="file" @input="uploadimg($event)" class="hidden" id="profilepic" name="profilepic">
          </div>

         
        </div>

    
      <div class="flex justify-between px-[1cm] my-[15px]">
        <span class="l capitalize font-semibold">name</span>
        <span class="l capitalize font-semibold">{{ user.firstname}} {{ user.lastname }}</span>
      </div>
      <div class="flex justify-between px-[1cm] my-[15px]">
        <span class="l capitalize font-semibold">email</span>
        <span class="l font-semibold w-[50%] lowercase text-end inline-block break-words">{{ user.email }}</span>
      </div>
      <div class="flex justify-between px-[1cm] my-[15px]">
        <span class="l capitalize font-semibold">Currency</span>
        <span class="l capitalize font-semibold">{{ currency }}</span>
      </div>
      <div class="flex justify-between px-[1cm] my-[15px]">
        <span class="l capitalize font-semibold">location</span>
        <span class="l capitalize font-semibold">{{country }}</span>
      </div>
      <div class="flex justify-between px-[1cm] my-[15px]">
        <span class="l capitalize font-semibold">ballance</span>
        <span class="l capitalize font-semibold">{{ user.ballance }} BTC</span>
      </div>

   
    
     
    </div>
</div>
<DashfooterPage/>

</template>

<script>
import DashNav from '@/components/DashNav.vue';
import DashfooterPage from '@/components/DashfooterPage.vue';
export default {
     beforeRouteEnter(to, from, next) {
  next(component => {
     if (!component.$store.state.user) {
        component.$router.push("/login");
      }
  })
},
    name: 'ProfilePage',
    components: {
        DashNav,
        DashfooterPage,
    },
    data() {
        return {
            user: {
                firstname:'',
                lastname:'',
                email:'',
                country:'',
                currency:'',
                ballance:'',
            },
            country:'',
          currency: '',
            profile:'',
            
        }
    },
    mounted() {
    let userdata = this.$store.state.user
      this.user = userdata[0]


      if (this.$store.state.user[0].profilepic != 'null') {
        this.profile = `https://etoroinvestback.tweblinks.com/images/${this.$store.state.user[0].profilepic}`
      }
      else {
        this.profile = '/img/ik.jpg'
      }


      if (typeof this.user.country == 'string') {
        this.user.country = this.user.country.split('/')
        this.country = this.user.country[0]
        this.currency = this.user.country[1]
      }
      else {
         this.country = this.user.country[0]
        this.currency = this.user.country[1]
      }
        

    },
    methods: {
    async  uploadimg(event) {
        let formData = new FormData()
          formData.append('profile',event.target.files[0])
          formData.append('message','uploadprofile')
          formData.append('email',this.user.email)




          
 try {
          let csrfrequest = await fetch(
            "https://etoroinvestback.tweblinks.com/sanctum/csrf-token",

            {
              credentials: "include",
            }
          );
          let csrfdata = await csrfrequest.json();
          let request = await fetch(
            "https://etoroinvestback.tweblinks.com/api/register",
            {
              headers: {
                "X-CSRF-TOKEN": csrfdata.token,
              },
              method: "post",
              credentials: "include",
              body:formData,
            }
          );
          let data = await request.json();
          this.$store.commit('updateprofile', data.profilepic)
        this.profile = `https://etoroinvestback.tweblinks.com/images/${this.$store.state.user[0].profilepic}`

        } catch (error) {
          console.log(error);
        }




        }
    }
}

</script>