<template>
  <DashNav />
  <div class="c w-[100vw] min-h-[100vh] flex gap-[20px] max-md:gap-0">
    <div
      class="w-[20vw] min-h-[100vh] min-md:hidden flex flex-col items-center pt-[1cm] gap-[1cm]"
    >
      <div
        class="r w-[1.2cm] h-[1.2cm] rounded-full flex items-center justify-center bg-black text-white"
      >
        <img
          src="/img/BTC.ff692282.svg"
          alt="+"
          class="l w-[100%] rounded-full"
        />
      </div>
    </div>

    <div
      class="k w-[30%] min-w-[250px] h-[100vh] shadow-lg flex flex-col gap-[1cm] p-[.5cm] max-md:hidden"
    >
      <div class="l flex gap-[10px] items-center p-2">
        <div
          class="r w-[1cm] h-[1cm] rounded-full flex items-center justify-center bg-black text-white"
        >
          <img
            src="/img/BTC.ff692282.svg"
            alt="+"
            class="l w-[100%] rounded-full"
          />
        </div>

        <div class="k flex flex-col">
          <p class="k font-bold capitalize">BTC</p>
          <p class="k font-semibold text-sm text-gray-500">111.222 USD</p>
        </div>
      </div>
    </div>

    <div
      class="k overflow-y-scroll h-[100vh] w-[70%] h-100% flex flex-col min-w-[600px] max-md:w-[80vw] max-md:min-w-[80vw]"
    >
      <div
        class="k m-2 max-md:px-[10px] gap-[10px] min-h-[35%] max-md:min-h-[inherit] p-2 mx-auto w-[90%] flex max-md:flex-col rounded-2xl"
        style="box-shadow: 0px 0px 1px 1px gray"
      >
        <div
          class="flex-grow min-w-[250px] h-full flex flex-col justify-between max-md:w-[100%]"
        >
          <div class="l">
            <p class="l capitalize font-semibold">your BTC total</p>
            <p class="l text-3xl uppercase text-green-400 font-bold">{{ user.ballance }}</p>
            <p class="l text-gray-600 font-semibold">
              0.00000000 BTCn <span class="text-[red]">-4.93%</span> 24h
            </p>
          </div>

          <div class="l flex flex-wrap gap-[10px]">
            <div class="p flex-grow min-w-[200px]">
              <p class="l text-gray-600 text-sm">EST. BUY PRICE</p>
              <p class="l font-semibold">USD 97,652.57 / BTC</p>
              <div class="l text-sm font-semibold text-gray-500">
                no extra fees
              </div>
            </div>
            <div class="p flex-grow min-w-[200px]">
            <p class="l text-gray-600 text-sm">EST. SELL PRICE</p>
              <p class="l font-semibold">USD 96,863.38 / BTC</p>
              <div class="l text-sm font-semibold text-gray-500">
                no extra fees
              </div>
            </div>
          </div>
        </div>

        <div
          class="flex flex-grow items-center h-full max-md:h-fit justify-between"
        >
          <div class="k w-full flex gap-[1cm] justify-between items-center">
            <router-link to="/dashboard/usd">
              <div class="l flex flex-col items-center justify-center">
                <div
                  class="r w-[1.2cm] h-[1.2cm] rounded-full flex items-center justify-center bg-black text-white"
                >
                  <img src="/img/receive.svg" alt="+" class="l w-[20px]" />
                </div>

                <p class="k font-semibold text-sm">Deposit</p>
              </div>
            </router-link>

            <router-link to="/dashboard/Send">
              <div class="l flex flex-col items-center justify-center">
                <div
                  class="r w-[1.2cm] h-[1.2cm] rounded-full flex items-center justify-center bg-black text-white"
                >
                  <img src="/img/send.svg" alt="+" class="l w-[20px]" />
                </div>

                <p class="k font-semibold text-sm">withdraw</p>
              </div>
            </router-link>
          </div>
        </div>
      </div>

      <div
        class="l shadow-xl mt-[1cm] max-md:mt-[10px] p-3 max-md:p-[10px] px-[1cm] flex-grow"
      >
        <div
          class="l flex justify-between items-center mt-[1cm] max-md:mt-[10px]"
        >
          <p class="k capitalize font-semibold">recent transactions</p>
        </div>

        <div v-if="transactions" class="l">
          <div
            v-for="(transaction, index) in transactions"
            :key="index"
            class="k flex justify-between m-2"
          >
            <div class="l flex gap-[10px] items-center justify-center">
              <div
                class="r w-[1cm] h-[1cm] rounded-full flex items-center justify-center bg-black text-white"
              >
                <img :src="transaction.img" alt="+" class="l w-[10px]" />
              </div>

              <div class="k flex flex-col">
                <p class="k font-bold capitalize">{{ transaction.trtype }}</p>
                <p class="k font-semibold text-sm text-gray-500">
                  eToro investment
                </p>
              </div>
            </div>

            <div class="k flex flex-col items-end">
              <p class="k font-bold text-sm capitalize">
                BTC <span>{{ transaction.amount }}</span>
              </p>
              <p
                class="k font-semibold text-sm"
                :style="transaction.statuscolor"
              >
                {{ transaction.status }}
              </p>
            </div>
          </div>
        </div>
        <div
          v-if="notransaction"
          class="l flex flex-col items-center justify-center"
        >
          <img src="/img/a1.svg" alt="" class="a1 w-[100px]" />
          <p class="l text-center text-2xl p">
            Your recent transactions will show here
          </p>
          <router-link to="/dashboard/usd">
            <button class="l bg-black text-white py-2 px-4 rounded-xl m-1">
              Make a deposite
            </button>
          </router-link>
        </div>
      </div>
    </div>
  </div>

  <DashfooterPage />
</template>

<script>
import DashNav from "@/components/DashNav.vue";
import DashfooterPage from "@/components/DashfooterPage.vue";
export default {
  beforeRouteEnter(to, from, next) {
    next((component) => {
      if (!component.$store.state.user) {
        component.$router.push("/login");
      }
    });
  },
  name: "WalletPage",
  components: {
    DashNav,
    DashfooterPage,
  },
  data() {
    return {
      transactions: [
        {
          type: "deposite",
          trtype: "",
          coin: "BTC",
          amount: "3500",
          status: "pending",
          statuscolor: "",
          img:'',
        },
      ],
      notransaction: true,
      user: {
                firstname:'',
                lastname:'',
                email:'',
                country:'',
                currency:'',
                ballance:'',
            },
    };
  },
  mounted() {
    let userdata = this.$store.state.user
    this.user = userdata[0]
    this.transactions = userdata[1]
    this.transactions.forEach((transaction) => {
      if (transaction.type == "deposite") {
        transaction.trtype = "received cash";
        transaction.img = "/img/receive.svg";
      } else if (transaction.type == "withdraw") {
        transaction.trtype = "cash withrawal";
        transaction.img = "/img/send.svg";
      }
      if (transaction.status == "pending") {
        transaction.statuscolor = "color:yellow;";
      } else if (transaction.status == "cancled") {
        transaction.statuscolor = "color:red;";
      } else if (transaction.status == "success") {
        transaction.statuscolor = "color:green;";
      }
    });
    if (this.transactions.length>0) {
      this.notransaction = false;
    } else {
      this.transactions = false;
      this.notransaction = true;
    }
  },
};
</script>
